.scoreboard {
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  max-width: 338px;
  width: 70%;
  height: 70%;
  border: 2px solid rgba(110, 43, 80, 0.9);
  border-radius: 10px;
  background-color: rgba(110, 43, 80, 0.9);
  -webkit-box-shadow: 0px 0px 25px 0px rgba(34, 201, 192, 0.384);
          box-shadow: 0px 0px 25px 0px rgba(34, 201, 192, 0.384);
}

.scoreboard-title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  padding-top: 0.5rem;
}

.scoreboard-header {
  border-bottom: 2px solid #22c7bf;
  border-radius: 2px;
  color: white;
  box-shadow: 0px 10px 15px -10px rgba(34, 201, 192, 0.384);
  -webkit-box-shadow: 0px 10px 15px -10px rgba(34, 201, 192, 0.384);
  -moz-box-shadow: 0px 10px 15px -10px rgba(34, 201, 192, 0.384);
  margin-bottom: 0.75rem;
}

.score-column {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 30%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.score-item {
  height: 100%;
  width: 100%;
  border-radius: 5px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 3px 0;
}

h1,
h2,
h3,
p {
  padding: 0;
  margin: 0;
}

.myScore {
  -webkit-box-shadow: 0px -7.5px 15px -10px rgba(34, 201, 192, 0.384);
          box-shadow: 0px -7.5px 15px -10px rgba(34, 201, 192, 0.384);
  margin-top: 0.5rem;
  padding-top: 0.5rem;
  border-top: 2px solid #22c7bf;
  font-size: 1.6rem;
  border-radius: 2px;
}

.scoreboard-new-game {
  letter-spacing: 1px;
  text-transform: uppercase;
  background-color: transparent;
  border: 2px solid white;
  font-size: 1.7rem;
  font-weight: bold;
  color: white;
  width: 9rem;
  height: 3.5rem;
  border-radius: 6px;
  cursor: pointer;
  -webkit-box-shadow: 0px 0px 12px -2px rgba(34, 201, 192, 0.384);
          box-shadow: 0px 0px 12px -2px rgba(34, 201, 192, 0.384);
  -webkit-box-shadow: inset 0px 0px 5px 0px rgba(34, 201, 192, 0.384);
          box-shadow: inset 0px 0px 5px 0px rgba(34, 201, 192, 0.384);
  margin-top: 1.75rem;
  margin-bottom: 2rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -user-select: none;
}

.scoreboard-new-game:hover {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  background-color: #22c7bf;
  color: white;
  -webkit-box-shadow: 0px 0px 15px 0px rgba(34, 201, 192, 0.384);
          box-shadow: 0px 0px 15px 0px rgba(34, 201, 192, 0.384);
  border: 2px solid #22c7bf;
}

.scoreboard-new-game:focus {
  outline: none;
}

.scoreboard-new-game:active {
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  border: 2px solid #1fada6;
  background-color: #1fada6;
  -webkit-box-shadow: 0px 0px 15px 2px rgba(31, 173, 166, 0.384);
          box-shadow: 0px 0px 15px 2px rgba(31, 173, 166, 0.384);
}

.textColor {
  margin: 0;
  padding: 0;
  color: #22c7bf;
  text-shadow: 0px 0px 15px rgba(34, 201, 192, 0.384);
}

.scoreboard-prev-score {
  width: 30%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-top: 2px solid #22c7bf;
  border-bottom: 2px solid #22c7bf;
  border-radius: 2px;
  margin: 0.75rem 0rem;
  color: white;
  font-size: 1.5rem;
}

@media screen and (max-height: 1080px) {
  .scoreboard {
    height: 70%;
  }
}

@media screen and (max-width: 390px) {
  .scoreboard {
    max-width: 100%;
  }
}

@media screen and (max-width: 340px) {
  .scoreboard-title {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .scoreboard-header {
    font-size: 1.25rem;
  }
  .score-item {
    font-size: 0.9rem;
  }
}

@media screen and (max-width: 1024px) and (max-height: 1366px) {
  .scoreboard {
    margin-top: 2rem;
    margin-bottom: 0.75rem;
    height: 40%;
  }
}

@media screen and (max-width: 1024px) and (max-height: 1366px) {
  .scoreboard {
    margin-top: 1.5rem;
    margin-bottom: 0.5rem;
    height: 45%;
  }
