@media screen and (max-width: 600px) and (max-height: 880px) {
  .scoreboard {
    margin-top: 1.5rem;
    margin-bottom: 0.5rem;
    height: 50%;
  }
  .scoreboard-prev-score {
    margin: 0;
    margin-bottom: 1rem;
  }
}

@media screen and (max-width: 495px) and (max-height: 810px) {
  .scoreboard {
    margin-top: 1.5rem;
    margin-bottom: 0.5rem;
    height: 60%;
  }
  .scoreboard-prev-score {
    margin: 0;
    margin-bottom: 1rem;
  }
}

@media screen and (max-width: 485px) {
  .scoreboard {
    min-width: 80%;
  }
}

@media screen and (max-width: 430px) {
  .scoreboard {
    min-width: 85%;
  }
}

@media screen and (max-width: 400px) {
  .scoreboard {
    min-width: 90%;
  }
}

@media screen and (max-width: 375px) {
  .scoreboard {
    min-width: 100%;
  }
}

@media screen and (max-height: 670px) {
  .scoreboard-prev-score {
    font-size: 1.5rem;
    margin: 0;
  }
}

@media screen and (max-height: 640px) {
  .scoreboard {
    height: 80%;
  }
  .scoreboard-prev-score {
    font-size: 1.25rem;
