body.active-modal {
  overflow-y: hidden;
}

.btn-modal {
  display: block;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: 0;
}

.btn-modal img {
  width: 35px;
  height: 35px;
  cursor: pointer;
}

.btn-modal img:hover {
  -webkit-transition: 250ms linear;
  transition: 250ms linear;
  -webkit-transform: scale(1.3);
  transform: scale(1.05);
}

.modal,
.overlay {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
}

.overlay {
  background: rgba(0, 0, 0, 0.6);
}

.modal-content {
  position: absolute;
  top: 41%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  line-height: 1.4;
  background: #0f292f;
  padding: 14px 28px;
  border-radius: 10px;
  max-width: 600px;
  min-width: 300px;
}

.close-modal {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px 7px;
  background-color: transparent;
  border: 0;
}

.close-modal img {
  display: block;
  margin: 0;
  padding: 0;
  margin-top: 5px;
  -webkit-filter: invert(100%);
  filter: invert(100%);
  width: 25px;
  height: 25px;
  cursor: pointer;
}

.close-modal img:hover {
  -webkit-transition: 250ms linear;
  transition: 250ms linear;
  -webkit-transform: scale(1.3);
  transform: scale(1.05);
}

.instructions {
  color: white;
  width: 90%;
  font-size: 1.5rem;
  font-weight: 400;
  margin: 0;
  text-align: center;
}

.instructionDIV {
  display: block;
  width: 35px;
  height: 35px;
  border: 2px solid white;
  border-radius: 5px;
  color: white;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0;
  margin: 0;
  cursor: pointer;
  -webkit-box-shadow: 0px 0px 12px -2px rgba(34, 201, 192, 0.384);
          box-shadow: 0px 0px 12px -2px rgba(34, 201, 192, 0.384);
  -webkit-box-shadow: inset 0px 0px 5px 0px rgba(34, 201, 192, 0.384);
          box-shadow: inset 0px 0px 5px 0px rgba(34, 201, 192, 0.384);
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -user-select: none;
  background-color: transparent;
  font-size: 1.5rem;
  font-weight: bold;
}

.instructionDIV:hover {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  background-color: #22c7bf;
  color: white;
  border: 2px solid #22c7bf;
  -webkit-box-shadow: 0px 0px 15px 0px rgba(34, 201, 192, 0.384);
          box-shadow: 0px 0px 15px 0px rgba(34, 201, 192, 0.384);
}

.instructionDIV:focus {
  outline: none;
}

.instructionDIV:active {
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  border: 2px solid #1fada6;
  background-color: #1fada6;
  -webkit-box-shadow: 0px 0px 15px 2px rgba(31, 173, 166, 0.384);
          box-shadow: 0px 0px 15px 2px rgba(31, 173, 166, 0.384);
}

@media screen and (max-width: 670px) {
  .close-modal img {
    display: none;
  }
}
