$bgClr: #0f292f;
$platformClr: #501f3a;
$textClr: white;
$diceClr: #6f2a50;
$btnClr: #22c7bf;
$scoreboardClr: hsla(327, 44%, 30%, 0.9);
$goldClr: hsl(45, 66%, 53%);
$silverClr: hsl(240, 4%, 67%);
$bronzeClr: hsl(34, 97%, 26%);

// hsla(327, 44%, 30%, 0.9)

.scoreboard {
	color: white;
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-direction: column;
	max-width: 338px;
	width: 70%;
	height: 70%;
	// height: max-content;
	// background-color: red;
	border: 2px solid $scoreboardClr;
	border-radius: 10px;
	// padding: 2rem 3rem;
	// margin-top: 2rem;

	background-color: $scoreboardClr;
	box-shadow: 0px 0px 25px 0px hsla(177, 71%, 46%, 0.384);
	// height: 30%;
	// overflow-y: scroll;
}

.scoreboard-title {
	display: flex;
	justify-content: space-around;
	align-items: center;
	// background-color: blue;
	width: 100%;
	// height: 100%;

	// padding: 0rem 1rem;
	padding-top: 0.5rem;

	// box-shadow: 0px 10px 15px -10px hsla(177, 71%, 46%, 0.384);
	// -webkit-box-shadow: 0px 10px 15px -10px hsla(177, 71%, 46%, 0.384);
	// -moz-box-shadow: 0px 10px 15px -10px hsla(177, 71%, 46%, 0.384);
}

.scoreboard-header {
	border-bottom: 2px solid $btnClr;
	border-radius: 2px;
	color: $textClr;

	box-shadow: 0px 10px 15px -10px hsla(177, 71%, 46%, 0.384);
	-webkit-box-shadow: 0px 10px 15px -10px hsla(177, 71%, 46%, 0.384);
	-moz-box-shadow: 0px 10px 15px -10px hsla(177, 71%, 46%, 0.384);

	margin-bottom: 0.75rem;
}

.score-column {
	// background-color: red;
	display: flex;
	width: 30%;
	// height: 100%;
	flex-direction: column;
	align-items: center;
	// background-color: red;
}

.score-item {
	// background-color: red;
	height: 100%;
	width: 100%;
	border-radius: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 3px 0;
}

h1,
h2,
h3,
p {
	padding: 0;
	margin: 0;
}

.myScore {
	box-shadow: 0px -7.5px 15px -10px hsla(177, 71%, 46%, 0.384);
	// -webkit-box-shadow: 0px 10px 15px -10px hsla(177, 71%, 46%, 0.384);
	// -moz-box-shadow: 0px 10px 15px -10px hsla(177, 71%, 46%, 0.384);

	margin-top: 0.5rem;
	padding-top: 0.5rem;
	border-top: 2px solid $btnClr;
	font-size: 1.6rem;
	border-radius: 2px;

	span {
		// color: $btnClr;
		// color: hsl(177, 71%, 50%);
	}
}

.scoreboard-new-game {
	letter-spacing: 1px;
	text-transform: uppercase;
	background-color: transparent;
	// border: 2px solid $btnClr;
	border: 2px solid $textClr;
	font-size: 1.7rem;
	font-weight: bold;
	// color: $btnClr;
	color: $textClr;
	width: 9rem;
	height: 3.5rem;
	border-radius: 6px;
	cursor: pointer;
	box-shadow: 0px 0px 12px -2px hsla(177, 71%, 46%, 0.384);
	box-shadow: inset 0px 0px 5px 0px hsla(177, 71%, 46%, 0.384);
	margin-top: 1.75rem;
	margin-bottom: 2rem;

	-webkit-user-select: none;
	-moz-user-select: none;
	-o-user-select: none;
	-user-select: none;
}

.scoreboard-new-game:hover {
	transition: all 0.5s ease;
	background-color: $btnClr;
	color: white;
	box-shadow: 0px 0px 15px 0px hsla(177, 71%, 46%, 0.384);

	border: 2px solid $btnClr;
}

.scoreboard-new-game:focus {
	outline: none;
}

.scoreboard-new-game:active {
	transition: all 0.2s ease;
	border: 2px solid hsl(177, 70%, 40%);
	background-color: #1fada6;
	box-shadow: 0px 0px 15px 2px hsla(177, 70%, 40%, 0.384);
}

.textColor {
	margin: 0;
	padding: 0;
	color: $btnClr;
	text-shadow: 0px 0px 15px hsla(177, 71%, 46%, 0.384);
}

.scoreboard-title {
	// padding: 1rem 3rem;
}
.scoreboard-prev-score {
	width: 30%;

	display: flex;
	justify-content: center;
	align-items: center;

	border-top: 2px solid $btnClr;
	border-bottom: 2px solid $btnClr;
	border-radius: 2px;

	margin: 0.75rem 0rem;
	color: $textClr;
	font-size: 1.5rem;
	// background-color: red;
}

