@media screen and (max-width: 390px) {
	.scoreboard {
		// max-width: 0px;
		max-width: 100%;
	}
}

@media screen and (max-width: 340px) {
	.scoreboard-title {
		justify-content: center;
	}
	.scoreboard-header {
		font-size: 1.25rem;
	}
	.score-item {
		font-size: 0.9rem;
	}
}

// both
@media screen and (max-width: 1024px) and (max-height: 1366px) {
	.scoreboard {
		margin-top: 2rem;
		margin-bottom: 0.75rem;
		height: 40%;
	}
}

@media screen and (max-width: 1024px) and (max-height: 1366px) {
	.scoreboard {
		margin-top: 1.5rem;
		margin-bottom: 0.5rem;
		height: 45%;
	}
}

@media screen and (max-width: 600px) and (max-height: 880px) {
	.scoreboard {
		margin-top: 1.5rem;
		margin-bottom: 0.5rem;
		height: 50%;
	}
	.scoreboard-prev-score {
		margin: 0;
		margin-bottom: 1rem;
	}
}

