		margin-bottom: 0.5rem;
		height: 60%;
	}
	.scoreboard-prev-score {
		margin: 0;
		margin-bottom: 1rem;
	}
}

@media screen and (max-width: 485px) {
	.scoreboard {
		min-width: 80%;
	}
}

@media screen and (max-width: 430px) {
