$bgClr: #0f292f;
$platformClr: #501f3a;
$textClr: white;
$diceClr: #6f2a50;
$btnClr: #22c7bf;

body.active-modal {
	overflow-y: hidden;
}

.btn-modal {
	display: block;
	margin: 0;
	padding: 0;

	background-color: transparent;
	border: 0;

	// margin-left: 8rem; // margin-right: 2.5rem;
	img {
		width: 35px;
		height: 35px;
		cursor: pointer;
		&:hover {
			transition: 250ms linear;
			-webkit-transform: scale(1.3);
			-ms-transform: scale(1.3);
			transform: scale(1.05);
		}
	}
}
.modal,
.overlay {
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	position: fixed;
}

.overlay {
	background: hsla(191, 42%, 0%, 0.6); //rgba(49, 49, 49, 0.8);
}

.modal-content {
	position: absolute;
	top: 41%;
	left: 50%;
	transform: translate(-50%, -50%);
	line-height: 1.4;
	background: $bgClr; // #f1f1f1;
	padding: 14px 28px;
	border-radius: 10px;
	max-width: 600px;
	min-width: 300px;
}

.close-modal {
	position: absolute;
	top: 10px;
	right: 10px;
	padding: 5px 7px;
	background-color: transparent;
	border: 0;

	img {
		display: block; //none;
		margin: 0;
		padding: 0;
		margin-top: 5px;
		// margin-right: 7px;

		-webkit-filter: invert(100%);
		filter: invert(100%);

		width: 25px;
		height: 25px;

		cursor: pointer;

		&:hover {
			transition: 250ms linear;
			-webkit-transform: scale(1.3);
			-ms-transform: scale(1.3);
			transform: scale(1.05);
		}
	}
}

.instructions {
	color: $textClr;
	width: 90%;
	font-size: 1.5rem;
	font-weight: 400;
	margin: 0;
	text-align: center;
}

.instructionDIV {
	display: block;

	width: 35px;
	height: 35px;
	border: 2px solid white;
	border-radius: 5px;
	color: white;
	// font-size: 1.3rem;

	display: inline-flex;
	justify-content: center;
	align-items: center;

	padding: 0;
	margin: 0;

	cursor: pointer;

	box-shadow: 0px 0px 12px -2px hsla(177, 71%, 46%, 0.384);
	box-shadow: inset 0px 0px 5px 0px hsla(177, 71%, 46%, 0.384);

	-webkit-user-select: none;
	-moz-user-select: none;
	-o-user-select: none;
	-user-select: none;

	background-color: transparent;
	// border: 2px solid $btnClr;
	font-size: 1.5rem;
	font-weight: bold;
	// color: white;

	&:hover {
		transition: all 0.5s ease;
		background-color: $btnClr;
		color: white;
		border: 2px solid $btnClr;
		box-shadow: 0px 0px 15px 0px hsla(177, 71%, 46%, 0.384);
	}
	&:focus {
		outline: none;
	}
	&:active {
		transition: all 0.2s ease;
		border: 2px solid hsl(177, 70%, 40%);
		background-color: #1fada6;
		box-shadow: 0px 0px 15px 2px hsla(177, 70%, 40%, 0.384);
	}
}

// margin-

@media screen and (max-width: 670px) {
	.close-modal {
		img {
			display: none;
		}
	}
}
